import { Box, Button, Flex, HoverCard, Inset, Link } from "@radix-ui/themes";
import ResponsiveContainer from "./ui/responsive-container";
import { ChevronDownIcon, HamburgerMenuIcon } from "@radix-ui/react-icons";

import sitemap from "@/sitemap.json";
import { content } from "@/components/nav-bar-content";
import { logo } from "@/images";

export default function NavBar() {
  return (
    <Box height="48px">
      <ResponsiveContainer>
        <Flex justify="between" align="center">
          <Flex align="center" gap="2">
            <HamburgerMenuIcon className="sm:hidden scale-[120%]" />
            <Link href={sitemap.home}>
              <img src={logo.src} alt={logo.alt} width="48px" height="48px" />
            </Link>
          </Flex>
          <Flex
            gap={{
              initial: "5",
              xl: "6",
            }}
            display={{
              initial: "none",
              xs: "flex",
            }}
          >
            {content.links.map((link, index) => {
              return link.menu ? (
                <HoverCard.Root key={"nav-bar-" + index}>
                  <HoverCard.Trigger>
                    <Link
                      weight="medium"
                      href={link.url}
                      size={{
                        initial: "2",
                        md: "4",
                      }}
                    >
                      {link.text} <ChevronDownIcon className="inline" />
                    </Link>
                  </HoverCard.Trigger>
                  <HoverCard.Content width="512px">
                    <Flex>
                      <Inset clip="padding-box" side="left" pr="current">
                        <img
                          src={link.menu.defaultImage}
                          alt={link.menu.defaultImageAlt}
                          style={{
                            display: "block",
                            objectFit: "cover",
                            width: "180px",
                            height: "256px",
                            backgroundColor: "var(--gray-5)",
                          }}
                        />
                      </Inset>
                      <Flex direction="column" gap="4" className="align-middle">
                        {link.menu.items.map((item, menuItemIndex) => (
                          <Flex
                            key={index + "-" + menuItemIndex}
                            align="center"
                          >
                            <Link weight="medium" href={item.url}>
                              {item.text}
                            </Link>
                            {item.after}
                          </Flex>
                        ))}
                      </Flex>
                    </Flex>
                  </HoverCard.Content>
                </HoverCard.Root>
              ) : (
                <Link
                  key={"nav-bar-" + index}
                  size={{
                    initial: "2",
                    md: "4",
                  }}
                  weight="medium"
                  href={link.url}
                >
                  {link.text}
                </Link>
              );
            })}
          </Flex>
          <Flex gap="4">
            {content.buttons?.map((button, index) => (
              <Button
                key={"nav-bar-button-" + index}
                asChild
                variant={
                  index < content.buttons!.length - 1 ? "outline" : "solid"
                }
              >
                <Link href={button.url} underline="none">
                  {button.icon}
                  {button.text}
                </Link>
              </Button>
            ))}
          </Flex>
        </Flex>
      </ResponsiveContainer>
    </Box>
  );
}
